.backshadow {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgb(200,200,200);
	opacity: 0;
	transition: opacity 0.4s ease-in-out;
	z-index: -1;

	&.show-menu {
		opacity: 0.8;
		z-index: 2;
	}
}


