.hamburger-btn {
	padding: 5px;
	position: absolute;
	top: 20px;
	left: 20px;
	z-index: 1;

	i {
		color: white;
		font-size: 2rem;
	}

	@media(min-width: 768px) {
		& {
			display: none;
		}
	}
}



